* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100%;
  width: 100%;
  font-family: 'Montserrat', serif;
  background-color: #EEEEEE;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@media screen and (min-width: 320px) and (max-width: 850px) and (orientation: landscape) {
  html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
}
.Section {
    height: 100vh;
    width: 100%;
}



#menu {
    z-index: 100;
    position: fixed;
    display: flex;
    width: 100%;
    height: 6vh;
    padding-left: 60px;
    padding-right: 60px;
}

#options {
    padding-top: 1.5vh;
    position: absolute;
    right: 60px;
    visibility: visible;
}

.menu__mobile {
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.menu__mobile_button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.menu__dark {
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
    /* background-color: #2b2b33; */
}

.menu__light {
    background-color: #eeeeee;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 1em;
    /* color: #000; */
    /* opacity: 0.8; */
}

.menu__mobile_options {
    width: 100%;
    z-index: 99;
    position: fixed;
    top: 6vh;
    /* background-color: aqua; */
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    padding-right: 30px;
}

.menu__mobile_option {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    text-decoration: none;
    height: 5.8vh;
}

@media screen and (max-width: 500px) {
    #menu {
        padding-left: 30px;
        padding-right: 30px;
        display: flex;
        justify-content: flex-end;
    }
    .menu__mobile {
        visibility: visible;
    }
    #options {
        visibility: hidden;
    }
}
/* 
@media (orientation: landscape) {
    
    #menu {
        height: 6vw;
    }
    
} */

#menu a {
    font-size: 14px;
    padding-right: 2vh;
    font-weight: 500;
    text-decoration: none;
}

.App {
  background-color: purple;
}

.main_orange {
  color: #F65070;
}

.main_red {
  color: #F65070;
}

.main_white {
  color: #E3E3E3;
}

.main_black {
