.text {
  text-align: left;
  text-justify: inter-cluster;
  font-size: 18px;
  font-weight: 300;
}
.home__cover_page {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient( rgba(0, 0, 0, 0.4), 
    rgba(0, 0, 0, 0.9)), url(/static/media/coverpage.ca87ab22.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-color: #131211;
}

#home__mid_div {
    text-align: center;
    position: absolute;
    top: 40%;
    width: 100%;
}

#home__footer {
    text-align: center;
    position: absolute;
    bottom: 0%;
    width: 100%;
    padding-bottom: 10%;
    padding-top: 3%;
}

.home__main_title {
    font-size: 80px;
    font-weight: 1000;
    width: 100%;
}

.home__mini_title {
    font-size: 45px;
    font-weight: 900;
    width: 100%;
}

.home__animation_title {
    font-size: 65px;
    font-weight: 800;
}

@media screen and (max-width: 800px) {
    .home__main_title {
        font-size: 70px;
        font-weight: 1000;
        width: 100%;
    }
    .home__mini_title {
        font-size: 35px;
        font-weight: 900;
        width: 100%;
    }
    .home__animation_title {
        font-size: 55px;
        font-weight: 800;
    }
    #home__footer {
        text-align: center;
        position: absolute;
        bottom: 15%;
        width: 100%;
        padding-bottom: 10%;
        padding-top: 3%;
    }
}

