        padding-bottom: 10%;
        padding-top: 3%;
    }
}

@media screen and (max-width: 380px) {
    .home__main_title {
        font-size: 50px;
        font-weight: 1000;
        width: 100%;
    }
    .home__mini_title {
        font-size: 25px;
        font-weight: 900;
        width: 100%;
    }
    .home__animation_title {
        font-size: 30px;
        font-weight: 800;
    }
    #home__mid_div {
        top: 30%;
    }
    #home__footer {
        text-align: center;
        position: absolute;
        bottom: 35%;
        width: 100%;
        padding-bottom: 10%;
        padding-top: 3%;
