#Contact {
    color: #2b2b33;
    display: flex;
    flex-direction: column;
}


.contact__banner {
    height: 50%;
    width: 100%;  
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(to left, rgba(149, 47, 120, 0.6), 
    rgba(77, 89, 153, 0.6)), url(/static/media/banner.dbfdf771.jpg);
    background-size: cover;
}

.contact__info{
    display: flex;
    flex-direction: column;
    height: 50%;  
    width: 100%;  
}

.contact__msg {
    font-size: 45px;
    font-weight: 800;
    color: #ffffff;
}

.contact__msg_2 {
    font-size: 20px;
    font-weight: 300;
    color: #ffffff;
}
/* nnn                                   */
.contact__info_box {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    height: 50%;
}

.contact__info_text {
    color: #6b6e6e;
    display: flex;
    align-items: center;
    font-size: 18px;
}

.contact__info a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #6b6e6e;
}

.contact__info a:hover {
    text-decoration: none;
    color: #F65070;
}

.contact__button_box {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact__download_button {
    text-decoration: none;
    text-align: center;
    width: 200px;
    height: 50px;
    border-radius: 35px;
    border: none;
    background-color: #F65070;
    color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

.contact__profile_pic_box {
    width: 100%;
    display: flex;
    justify-content: center;
}

.contact__profile_picture {
    border-radius: 50%;
    border: solid 4px #eeeeee;
    float: left;
    height: 150px;
    width: 150px;
}

.contact__download_button:hover {
    background-color: #fa6e89;
}

